
import axios from 'axios';
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import cookie from 'vux/src/tools/cookie/index.js'


export default {
directives: {
TransferDom
},
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,
Alert,
XDialog
},
data() {
return {
departmentid: this.$store.getters.user.departmentid,
partyBranch: '',
address: '',
honor: '',
people: '',
todoList: [],
activeTitle:'',
info:[],
dateTimes:'',
showQrcodeDialog: false
};
},
methods: {
refer (item){
if(item.type == 'SCORE'){
this.$router.push({
path:'points/evaluation'
})
}else if(item.type == 'ACTIVE'){if(item.beginYn=='Y'){
this.$router.push({
path:'active/partyBranch'
})}else{ this.activeTitle = item.name;
this.showQR(item.masId);}
}
},
getDate(){
let datime = new Date().getHours();
if(datime>=5 & datime<8){
this.dateTimes = '早上好'
}
else if(datime>=8 & datime<11){
this.dateTimes = '上午好'

}
else if(datime>=11 & datime<13){
this.dateTimes = '中午好'
}
else if(datime>=13 & datime<19){
this.dateTimes = '下午好'
}
else{
this.dateTimes = '晚上好'
}
},
showQR(data){
document.getElementById('fei').src = 'http://www.dlbdata.cn/dangjian/active/showQrCode?activeId='+data;
this.showQrcodeDialog = true;
},
getDepartment() {
axios({
method: 'get',
url: 'pdepartment/queryById',
params: {
departmentid: this.departmentid

}
}).then((res) => {
this.info = res.data;
this.partyBranch = res.data.partyBranch;
this.address = res.data.address;
this.honor = res.data.honor;
this.people = res.data.people;
}).catch(function (error) {
console.log(error);
});
axios
.get('pscoredetail/todoList', {
params: {
userId: this.$store.getters.user.userid
}
})
.then(res => {
this.todoList = res.data;
})
.catch(err => {
console.log(err);
});
}

}, mounted() {
this.getDepartment();
this.getDate();
}


};



